<template>
  <div class="vue_table">
        <!--  data                           array
          ref vue.ref     
          show-summary	  	      Boolean
          summary-method	    	  Function
          row-key        Key        Function
                          Table 
                        reserve-selection 
                        
                        
          stripe         table	         boolean
          size	        Table 	              string	medium / small / mini
    Events
          selection-change    Function
          select-all         Checkbox   Function
          cell-click	          Function
          row-click           
    -->
    <el-table
      :border="border"
      :data="tableData.datas"
      :ref="tableRef"
      :show-summary="showSummary"
      :summary-method="showSummary?getSummaries:undefined"
      select-on-indeterminate
      :row-key="rowKey"
      :stripe ="stripe"
      :size="tableSize? tableSize:'small'"
      style="width:100%"
      @selection-change="handleSelectionChange"
      @select-all="selectAll"
      @cell-click="cellClick"
      @row-click="rowClick"
       >
            <!--
        width	          	              string
        min-width                    string
        fixed	               string, boolean	  true, left, right
                        true 
        align	                             String	         left/center/right
        header-align	     String	left/center/right
                        
        sortable	               boolean, string	 true, false, 'custom'
        class-name	     className	          string

        reserve-selection	 type=selection              Boolean
                           Boolean
                          true 
                           row-key

        type               selection     string   selection/index/expand
                           index 
                           1 
                           expand 

        index	             type=index    number, Function(index)
                          index 
        resizable                   boolean   true
                         el-table  border          
     -->
      <!--  -->
      <el-table-column
        v-if="tableData.selection"
        :width="tableData.selection.width"
        :min-width="tableData.selection.minWidth"
        :fixed="tableData.selection.fixed"
        :align="tableData.selection.align"
        :header-align="tableData.selection.headerAlign"
        :sortable="tableData.selection.sortable"
        type="selection"
        :index="tableData.selection.index"
        :resizable="tableData.selection.resizable"
        :class-name="tableData.selection.className"
        :reserve-selection="tableData.selection.select"
      />
      <template v-for="title in tableData.titles">
        <!--  -->
        <!-- 
          title.columnType  
              nomal      
              input   
              img     
              btn     
              slectBtn  

            -->
          <el-table-column
          v-if="title.columnType === 'nomal'||!title.columnType"
          :key="title.prop"
          :prop="title.prop"
          :label="title.label"
          :width="title.width"
          :min-width="title.minWidth"
          :fixed="title.fixed"
          :align="title.align"
          :header-align="title.headerAlign"
          :sortable="title.sortable"
          :type="title.type"
          :index="title.index"
          :resizable="title.resizable"
          :class-name="title.className"
        />
          <!--   input-->
        <el-table-column
          v-if="title.columnType === 'input'"
          :key="title.prop"
          :label="title.label"
          :width="title.width"
          :min-width="title.minWidth"
          :fixed="title.fixed"
          :align="title.align"
          :header-align="title.headerAlign"
          :sortable="title.sortable"
          :type="title.type"
          :index="title.index"
          :resizable="title.resizable"
          :class-name="title.className"
        >
             <!-- slot-scope="scope" scopedata -->
          <template slot-scope="scope">
            <el-input
              v-model="scope.row[title.prop]"
              :placeholder="title.placeholder"
              class="slotInput"
              @blur="setSort(scope.row)"/>
          </template>
        </el-table-column>
        <!--   img-->
        <el-table-column
          v-if="title.columnType === 'img'"
          :key="title.prop"
          :label="title.label"
          :width="title.width"
          :min-width="title.minWidth"
          :fixed="title.fixed"
          :align="title.align"
          :header-align="title.headerAlign"
          :sortable="title.sortable"
          :type="title.type"
          :index="title.index"
          :resizable="title.resizable"
          :class-name="title.className"
        >
          <template slot-scope="scope">
            <img :src="scope.row[title.prop]" class="slotImg" style="width:50px;height:50px;" >
            </template>
               </el-table-column>

                <!--  btnList
        resizable 
      -->
        <el-table-column
          v-if="title.columnType === 'btn'"
          :key="title.prop"
          :label="title.label"
          :width="title.width"
          :min-width="title.minWidth"
          :fixed="title.fixed"
          :align="title.align"
          :header-align="title.headerAlign"
          :resizable="title.resizable"
          :class-name="title.className"
          :type="title.type"
        >
              <template slot-scope="scope" >
                  <!--  btnview-->
            <el-button
              v-for="btn in title.btns"
              :key="btn.type"
              :type="btn.btnType?btn.btnType:'text'"
              :disabled="btn.disable"
              :class="{'btnTextColor':!scope.row.isEnable&&btn.select}"
              size="small"
              @click="handleClick(btn,scope.row,scope)">{{ btn.select? scope.row.isEnable ? btn.name:btn.unselectName  : btn.name }}</el-button>
               
       </template> 
        </el-table-column>
        <!--  -->
        <el-table-column
        v-if="title.columnType === 'btnDropdown'"
        :key="title.prop"
        :label="title.label"
        :width="title.width"
        :min-width="title.minWidth"
        :fixed="title.fixed"
        :align="title.align"
        :header-align="title.headerAlign"
        :resizable="title.resizable"
        :class-name="title.className"
        :type="title.type"
        >
      >
    <template slot-scope="scope">
      <div class="dropCommom">
    <el-dropdown :trigger="title.trigger" @command="handleClick($event,scope.row,scope)">
      <span class="el-dropdown-link">
        <i class="el-icon-arrow-down el-icon--right"/>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item
          v-for=" btn in title.btns"
          :key="btn.type"
          :command="btn.type">{{ btn.name }}</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>

        </template>
      </el-table-column>
      <!-- switch-->
         <el-table-column
          v-if="title.columnType === 'switch'"
          :key="title.prop"
          :label="title.label"
          :width="title.width"
          :min-width="title.minWidth"
          :fixed="title.fixed"
          :align="title.align"
          :header-align="title.headerAlign"
          :resizable="title.resizable"
          :class-name="title.className"
          :type="title.type"
        >
              <template slot-scope="scope">

                  <el-switch
                    v-for="swh in title.btns"
                    :key="swh.type"
                    v-model="scope.row[title.prop]"
                    :active-color="title.activeColor"
                    :inactive-color="title.inactiveColor"
                    :active-text="swh.name"
                    @change="switchClick(scope.row[title.prop],scope.row,scope)"
                  >
                  </el-switch>
               
       </template> 
        </el-table-column>
      </template>
      </el-table>
      <el-pagination
      v-if="pageData.total"
      :page-size="pageData.pageSize"
      :current-page="pageData.currentPage ? pageData.currentPage : 1"
      :total="pageData.total"
      small
      background
      layout="prev, pager, next, total, jumper"
      @current-change="handleCurrentChange"
    />
        </div >
      </template>
      <script>
        export default {
          name:'MTable',
          props:{
                tableData: { // 
                type: Object,
                default: () => { 
                  return {
                    datas:{ //
                       type: Object,
                       default: ()=>{
                         return []
                       }
                    }
                  } 
                  }
                 },
               showSummary:{ //
                 type:Boolean,
                 required: false,
                 default:false
               },
               stripe:{ // table	  
                 type:Boolean,
                 required: false,
                 default:true 
               },
               border:{ //table	  
                 type:Boolean,
                 required: false,
                 default:false 
               },
               tableSize:{ //table
                  type:String,
                 required: false,
                 default:"small" 
               },
               tableRef:{ //tableref
                 type:String,
                 default:'tableRef'
               },
                  pageData: {
                  type: Object,
                  required: false,
                  default: () => { return {} }
                },
          },
          data() {
            return {
              
            }
          },
          mounted () {
            
          },
          methods: {
                    rowKey(row) {
                      return row.id
                    },
                    handleSelectionChange(val) { //
                    this.$emit('handleSelectionChange', val)
                  },
                      selectAll(selection) { // Checkbox 
                    this.$emit('selectAll', selection)
                  },
                  cellClick(row, column, cell, event) { // 
                    this.$emit('cellClick', row, column, cell, event)
                  },
                  rowClick(row, column, event) { // 
                    this.$emit('rowClick', row, column, event)
                  },
                   getSummaries(params) { //   
                    return this.$parent.getSummaries(params)
                  },
                    setSort(row) { //
                    this.$emit('setSort', row)
                  },
                      handleClick(type, row, all) { //
                    
                      // console.log(row,2222);
                    this.$emit('handleBtnClick', type, row, all)
                  },
                      switchClick(type, row, all) { //swich
                    this.$emit('switchClick', type, row, all)
                  },
                  handleCurrentChange(val) { //
                    this.$emit('pageChage', val)
                  },
          },
        }
      
      </script>
      <style  scoped>
        .vue_table .el-pagination{
          text-align: center;
          margin-top: 20px;
        }
        /* .vue_table{
          min-height: 500px;
        } */
        .vue_table .el-dropdown{
          cursor: pointer;
        }
        .vue_table .btnTextColor{
          color: red;
        }
        
      </style>